@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* general css */
* {
  margin: none;
  box-sizing: border-box;
  padding: none;
}

body {
  font-family: 'Roboto', sans-serif;
}

#root {
  background-color: whitesmoke;
  min-height: 100vh;
  position: relative;
}

.Domu {
  padding-bottom: 300px;
}

.Domu .articles {
  width: 60%;
  margin: auto;
}

.Domu .articles .articleWrapper {
  margin: 20px 0;
  background-color: #d6d6d6;
  display: flex;
}

.Domu .articles .articleWrapper p {
  margin-top: 7px;
  font-size: 1.6em;
}

.Domu .articles .articleWrapper img {
  width: 20%;
  max-width: 200px;
  border-radius: 4px;
  display: block;
  padding: 7px;
}

nav {
  background: whitesmoke;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 25px;
}

nav .contact {
  display: inline-block;
  background-color: #2896d6;
  padding: 10px 20px;
  font-size: 1.2em;
  color: whitesmoke;
  border-radius: 2.5px;
  transition: 0.3s;
}

nav .contact:hover {
  background-color: #2082bb;
}

.Kontakt {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-bottom: 300px;
}

.Kontakt p {
  font-size: 1.3em;
}

.Kontakt p .blue-clr {
  color: #2896d6;
}

.Kontakt form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #d6d6d6;
  padding: 20px 30px;
  border: 1px solid black;
  border-radius: 2.5px;
}

.Kontakt form label {
  margin: 10px 0;
}

.Kontakt form label input {
  margin-left: 10px;
  width: 200px;
  font-family: "Roboto", sans-serif;
}

.Kontakt form textarea {
  width: 250px;
  height: 90px;
  font-family: "Roboto", sans-serif;
}

.Kontakt form input[type="submit"] {
  margin-top: 25px;
  font-family: "Roboto", sans-serif;
  padding: 7px 14px;
  background-color: #2896d6;
  border: none;
  font-size: 1em;
  color: whitesmoke;
  border-radius: 2.5px;
  cursor: pointer;
  transition: 0.3s;
}

.Kontakt form input[type="submit"]:hover {
  background-color: #2082bb;
}

footer {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
  background-color: #9bd1eb;
  height: 250px;
  padding-right: 30px;
  position: absolute;
  bottom: 0;
  width: 100%;
}

footer .contact {
  display: inline-block;
  background-color: #2896d6;
  padding: 10px 20px;
  font-size: 1.2em;
  color: whitesmoke;
  border-radius: 2.5px;
  transition: 0.3s;
}

footer .contact:hover {
  background-color: #2082bb;
}
